<template>
  <div>
    <!-- 面包屑组件 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>权限管理</el-breadcrumb-item>
      <el-breadcrumb-item>角色列表</el-breadcrumb-item>
    </el-breadcrumb>

    <!-- 卡片视图 -->
    <el-card>
      <!-- 添加角色按钮区域 -->
      <el-row>
        <el-col>
          <el-button type="primary" @click="addDialogVisible = true"
            >添加角色</el-button
          >
        </el-col>
      </el-row>
      <!-- 添加角色对话框 -->
      <el-dialog title="添加角色" :visible.sync="addDialogVisible" width="30%">
        <el-form
          :model="roleForm"
          :rules="addRolesRules"
          ref="roleFormRef"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-form-item label="角色名称" prop="roleName">
            <el-input v-model="roleForm.roleName"></el-input>
          </el-form-item>
          <el-form-item label="角色描述" prop="roleDesc">
            <el-input v-model="roleForm.roleDesc"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="addDialogVisible = false">取 消</el-button>
          <el-button type="primary " @click="addRole()">确 定</el-button>
        </span>
      </el-dialog>

      <!-- 编辑角色对话框 -->
      <el-dialog title="编辑角色" :visible.sync="editDialogVisible" width="30%">
        <el-form
          :model="editForm"
          :rules="editFormRule"
          ref="editFormRef"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-form-item label="角色ID" prop="roleId">
            <el-input disabled v-model="editForm.roleId"></el-input>
          </el-form-item>
          <el-form-item label="角色名称" prop="roleName">
            <el-input v-model="editForm.roleName"></el-input>
          </el-form-item>
          <el-form-item label="角色描述" prop="roleDesc">
            <el-input v-model="editForm.roleDesc"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="editDialogVisible = false">取 消</el-button>
          <el-button type="primary " @click="editRoleSubmit()">确 定</el-button>
        </span>
      </el-dialog>

      <!-- 分配权限的对话框 -->
      <el-dialog title="分配权限" 
      :visible.sync="setRightDialogVisible" 
      width="50%" 
      @close="setRightDialogClose()">
       <!-- 树形控件 -->
       <el-tree 
       :data="rightsList" 
       :props="treeProps" 
       show-checkbox
       node-key="id"
       default-expand-all
       ref="treeRef"
       :default-checked-keys="defKeys"
      ></el-tree>
        <span slot="footer" class="dialog-footer">
          <el-button @click="setRightDialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="allotRights()">确 定</el-button>
        </span>
      </el-dialog>

      <!-- 删除的弹窗 -->

      <el-table :data="rolesList" border stripe >
        <!-- 展开列 -->
        <el-table-column type="expand">
          <template slot-scope="scope">
            <el-row
              :class="['vcenter', 'bdbottom', index1 === 0 ? 'bdtop' : '']"
              v-for="(item1, index1) in scope.row.children"
              :key="index1"
            >
              <!-- 渲染一级权限 -->
              <el-col :span="5">
                <el-tag closable @close="removeById(scope.row, item1.id)">{{
                  item1.authName
                }}</el-tag>
                <i class="el-icon-caret-right"></i>
              </el-col>
              <!-- 渲染二级和三级权限 -->
              <el-col :span="19">
                <!-- 通过 for 循环渲染二级权限 -->
                <el-row
                  :class="['vcenter', index2 === 0 ? '' : 'bdtop']"
                  v-for="(item2, index2) in item1.children"
                  :key="index2"
                >
                  <el-col :span="6">
                    <el-tag
                      closable
                      @close="removeById(scope.row, item2.id)"
                      type="success"
                      >{{ item2.authName }}</el-tag
                    >
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <el-col :span="18">
                    <el-tag
                      closable
                      @close="removeById(scope.row, item3.id)"
                      type="warning"
                      v-for="(item3, index3) in item2.children"
                      :key="index3"
                    >
                      {{ item3.authName }}
                    </el-tag>
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <!-- 索引列 -->
        <el-table-column type="index"></el-table-column>
        <el-table-column label="角色名称" prop="roleName"> </el-table-column>
        <el-table-column label="角色描述" prop="roleDesc"></el-table-column>
        <el-table-column label="操作" width="300px">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              icon="el-icon-edit"
              @click="showEidtRole(scope.row.id)"
              >编辑</el-button
            >
            <el-button
              size="mini"
              type="danger"
              icon="el-icon-delete"
              @click="delRole(scope.row.id)"
              >删除</el-button
            >

            <el-button size="mini" type="warning"
             icon="el-icon-setting" @click="showSetRightDialog(scope.row)"
              >分配权限</el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </el-card>
  </div>
</template>

<script>
export default {
  name: "Roles",
  data() {
    return {
      // 所有角色列表数据
      rolesList: [],
      addDialogVisible: false,
      editDialogVisible: false,
      setRightDialogVisible: false,
      // 添加用户角色信息
      roleForm: {
        roleName: "",
        roleDesc: "",
      },
      // 查询到的用户角色
      editForm: {},
      addRolesRules: {
        roleName: [
          {
            required: true,
            message: "请输入角色名称",
            trigger: "blur",
          },
        ],
        roleDesc: [
          {
            required: true,
            message: "请输入角色描述",
            trigger: "blur",
          },
        ],
      },
      editFormRule: {
        roleName: [
          {
            required: true,
            message: "请输入角色名称",
            trigger: "blur",
          },
        ],
        roleDesc: [
          {
            required: true,
            message: "请输入角色描述",
            trigger: "blur",
          },
        ],
      },
      // 所有权限的数据
      rightsList: [],
      // 树形控件的属性绑定事件
      treeProps: {
        label: 'authName',
        children: 'children'
      },
      // 默认选中得的节点的id值
      defKeys: [],
      // 当前即将分配权限的ID
      roleId: ''
    };
  },
  created() {
    // 获取数据
    this.getRolesList();
  },
  methods: {
    // 获取所有角色的列表
    async getRolesList() {
      const { data: res } = await this.$http.get("/roles");
      if (res.meta.status !== 200) {
        return this.$message.error("获取角色列表失败");
      }

      this.rolesList = res.data;
    },
    // 添加用户
    addRole() {
      this.$refs.roleFormRef.validate(async (validata) => {
        if (!validata) return;
        // 发起网络请求
        const { data: res } = await this.$http.post("/roles", this.roleForm);
        console.log(res);
        if (res.meta.status !== 201) {
          return this.$message.error("添加用户失败");
        }
        this.$message.success("添加用户成功");
        this.addDialogVisible = false;
        this.getRolesList();
      });
    },
    // 显示编辑用户
    async showEidtRole(id) {
      this.editDialogVisible = true;
      const { data: res } = await this.$http.get("/roles/" + id);
      if (res.meta.status !== 200) {
        return this.$message.error("查询用户信息失败");
      }
      this.editForm = res.data;
      console.log(res.data);
    },
    // 提交编辑用户
    editRoleSubmit() {
      this.$refs.editFormRef.validate(async (validata) => {
        if (!validata) {
          return;
        }
        const { data: res } = await this.$http.put(
          "/roles/" + this.editForm.roleId,
          {
            roleName: this.editForm.roleName,
            roleDesc: this.editForm.roleDesc,
          }
        );
        if (res.meta.status !== 200) {
          return this.$message.error("修改信息失败");
        }
        this.$message.success("修改信息成功");
        this.editDialogVisible = false;
        this.getRolesList();
      });
    },
    // 删除用户
    async delRole(id) {
      const confirmRes = await this.$confirm(
        "此操作将永久删除该角色, 是否继续?",
        "提示",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
        }
      ) // catch 取消事件的捕捉并返回
        .catch((res) => res);

      if (confirmRes === "cancel") {
        return this.$message.info("已取消删除");
      }

      const { data: res } = await this.$http.delete("roles/" + id);

      if (res.meta.status !== 200) {
        return this.$message.error("删除角色失败,错误信息:" + res.meta.mgs);
      }
      this.$message.success("删除角色成功");
      this.getRolesList();
    },
    // 根据id删除用户权限
    async removeById(role, rightId) {
      const confirmRes = await this.$confirm(
        "此操作将永久删除该角色, 是否继续?",
        "提示",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
        }
      ) // catch 取消事件的捕捉并返回
        .catch((res) => res);
      if (confirmRes !== "confirm") {
        return this.$message.info("取消删除成功");
      }

      const { data: res } = await this.$http.delete(
        `/roles/${role.id}/rights/${rightId}`
      );
      if (res.meta.status !== 200) {
        return this.$message.error("删除权限失败");
      }
      this.$message.success("删除权限成功");
      role.children = res.data;
    },
    // 展示分配权限的对话框
   async showSetRightDialog(role) {
     this.roleId = role.id
      // 获取所有权限列表
      const {data : res} = await this.$http.get('/rights/tree')
      if (res.meta.status !== 200) {
        return this.$message.error("获取权限数据失败！")
      }
      // 把获取到的权限数据保存到data中
      this.rightsList = res.data
      // 通过递归获取三级节点的id
      this.getLeafKeys(role, this.defKeys)
      this.setRightDialogVisible = true
    },
    // 通过递归的形式获取 角色下所有三级权限的id，并保存到奥defKeys中
    getLeafKeys(node, arr) {
      // 如果当前node节点不包括children属性，则是三级节点
      if (!node.children) {
        return arr.push(node.id)
      }

      node.children.forEach( item => {
        this.getLeafKeys(item, arr)
      })
      
    },
    // 监听分配权限的对话框的关闭事件
    setRightDialogClose() {
      this.defKeys = []
    },
    // 点击为角色分配权限
    async allotRights() {
      const keys = [
        ...this.$refs.treeRef.getCheckedKeys(),
        ...this.$refs.treeRef.getHalfCheckedKeys()
      ]
      const idStr = keys.join(',')
      const {data : res } = await this.$http.post(`/roles/${this.roleId}/rights/`,{rids: idStr})
      console.log(res);
      if (res.meta.status !== 200) {
        return this.$message.error('分配权限失败')
      }
      this.$message.success('分配权限成功')
      this.setRightDialogVisible = false
      this.getRolesList()
    }
  },
};
</script>

<style scoped>
.el-tag {
  margin: 7px;
}

.bdtop {
  border-top: 1px solid #eee;
}
.bdbottom {
  border-bottom: 1px solid #eee;
}
.vcenter {
  display: flex;
  align-items: center;
}

</style>